import Vue from 'vue'
import VueRouter from 'vue-router'
import Page from '@/views/Page.vue'
import PageNext from '@/views/PageNext.vue'
import Print from '@/views/Print.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/design/',
    name: 'page',
    component: Page
  },  
  {
    path: '/design/:id',
    name: 'pageNext',
    component: PageNext
  },  
  {
    path: '/design/print-page/:id',
    name: 'Print',
    component: Print
  },  
  // {
  //   path: '/design/page/:id',
  //   name: 'nextPage',
  //   component: PageNext
  // }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
